<template>
  <v-card
    height="100%"
  >
    <div
      id="investigacion-investigadores-nivel-categoria"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Categoría.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('investigacion-investigadores-nivel-categoria')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as investigadores/as.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            :data="chartData"
            :options="chartOptions"
            type="PieChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Nivel / Categoría
                </th>
                <th class="text-left">
                  Cargos
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in NivelCategoriaData"
                :key="index"
              >
                <td>{{ parseNivelCategoria(index) }}</td>
                <td>{{ item }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['investigadoresCargos', 'mobileSize']),
    chartData () {
      return [
        ['Tipo de dedicación', 'investigadores'],
        ['Profesores (Prof. Titular+Prof. Asociado+Prof. Adjunto+A+B+C)', this.NivelCategoriaData.prof],
        ['JTP+D1', this.NivelCategoriaData.jtp],
        ['Ayudantes de 1ra+D2', this.NivelCategoriaData.ayu]
      ]
    },
    chartOptions () {
      return {
        chartArea: {
          left: 40,
          right: 40,
          top: 40,
          bottom: 40
        },
        colors: this.colors,
        backgroundColor: { fill: 'transparent' },
        height: 350,
        bar: { groupWidth: '70%' },
        pieHole: '0.2',
        sliceVisibilityThreshold: 0.0001,
        legend: {
          position: 'right',
          alignment: 'center',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    NivelCategoriaData () {
      return this.investigadoresCargos.reduce((acc, cur) => {
        const nivA = new RegExp('Niv A', 'gm')
        const nivB = new RegExp('Niv B', 'gm')
        const nivC = new RegExp('Niv C', 'gm')
        const nivD1 = new RegExp('Niv D1', 'gm')
        const nivD2 = new RegExp('Niv D2', 'gm')
        const profesores = nivA.test(cur.descripcion_categoria) ||
          nivB.test(cur.descripcion_categoria) ||
          nivC.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Profesor Asociado' ||
          cur.descripcion_categoria === 'ASOCIADO SIMPLE' ||
          cur.descripcion_categoria === 'Profesor Titular' ||
          cur.descripcion_categoria === 'Profesor Adjunto' ||
          cur.descripcion_categoria === 'ADJUNTO SIMPLE'
        const d1jtp = nivD1.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Jefe Trab. Practicos' ||
          cur.descripcion_categoria === 'Jef Trab Pract NM' ||
          cur.descripcion_categoria === 'JTP SIMPLE'
        const d2ayu = nivD2.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Ayudante de 1ra.' ||
          cur.descripcion_categoria === 'AYUDANTE DE 1º SIMPL'
        return profesores
          ? { ...acc, prof: acc.prof + 1 }
          : d1jtp
            ? { ...acc, jtp: acc.jtp + 1 }
            : d2ayu
              ? { ...acc, ayu: acc.ayu + 1 }
              : acc
      },
      {
        prof: 0,
        jtp: 0,
        ayu: 0
      }
      )
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    },
    parseNivelCategoria (v) {
      return v === 'prof'
        ? 'Profesores (Prof. Titular+Prof. Asociado+Prof. Adjunto+A+B+C)'
        : v === 'jtp'
          ? 'JTP+D1'
          : v === 'ayu'
            ? 'Ayudante de 1ra+D2'
            : 'No declara'
    }
  }
}
</script>
